import { acttype } from "../action-type"

const initialstate={
    list:["shdakjhkjash","jhasdjkhdsjka"]
}
export const notereducer=(state=initialstate,action)=>
{
    switch(action.type)
    {
        case acttype.ADD_NOTE:
            return {...state,list:[...state.list,action.payload]}
            default:
                return state
    }
}