import React, { useState } from 'react'
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import PauseIcon from '@mui/icons-material/Pause';
import './midbar.css'
const Middlebar = () => {
    // const [btn,btnSelect]=useState()
    const btnChoose=(e)=>{
        e.target.style.backgroundColor="rgb(10, 159, 110)";
        if(e.target.textContent==='Albums')
        {
            e.target.nextSibling.style.backgroundColor="beige";
            e.target.nextSibling.nextSibling.style.backgroundColor="beige";
            document.querySelector(".Artists").style.display="none";
            document.querySelector(".Songs").style.display="none";
        }
        else if(e.target.textContent==='Artists')
        {
            e.target.previousSibling.style.backgroundColor="beige";
            e.target.nextSibling.style.backgroundColor="beige";
            document.querySelector(".Albums").style.display="none";
            document.querySelector(".Songs").style.display="none";
        }
        else
        {
                e.target.previousSibling.style.backgroundColor="beige";
                e.target.previousSibling.previousSibling.style.backgroundColor="beige";
                document.querySelector(".Albums").style.display="none";
                document.querySelector(".Artists").style.display="none";
        }
       document.querySelector("."+e.target.textContent).style.display="flex";
    }
  return (
    <div className='middlebar'>
        <p className='midhead'>Sangeet</p>
        <div className="btn_choose"><label onClick={btnChoose}>Albums</label><label onClick={btnChoose}>Artists</label><label onClick={btnChoose}>Songs</label></div>
        <div className="mid_container">
            <div className="Albums hide">
                <p>Albums</p>
                <div className="album_container">
                <div><img src="./images/bhoot.jpg" alt="" /><PlayArrowIcon className='mid_icon'/><label>BHOOT POLICE</label></div><div><img src="./images/sholay.jpg" alt="" /><PlayArrowIcon className='mid_icon'/><label>BHOOT POLICE</label></div><div><img src="./images/pathan.jpg" alt="" /><PlayArrowIcon className='mid_icon'/><label>BHOOT POLICE</label></div><div><img src="./images/hero.jpg" alt="" /><PlayArrowIcon className='mid_icon'/><label>BHOOT POLICE</label></div>
                
                <div><img src="./images/bhoot.jpg" alt="" /><PlayArrowIcon className='mid_icon'/><label>BHOOT POLICE</label></div><div><img src="./images/sholay.jpg" alt="" /><PlayArrowIcon className='mid_icon'/><label>BHOOT POLICE</label></div><div><img src="./images/pathan.jpg" alt="" /><PlayArrowIcon className='mid_icon'/><label>BHOOT POLICE</label></div><div><img src="./images/hero.jpg" alt="" /><PlayArrowIcon className='mid_icon'/><label>BHOOT POLICE</label></div>
                </div>
           </div>
        
        
        <div className="Artists hide">
                <p>Artists</p>
                <div className="artist_container">
                    <label>asas</label><label>dasdas</label><label>asdasas</label>
                    <label>asas</label><label>dasdas</label><label>asdasas</label>
                    <label>asas</label><label>dasdas</label><label>asdasas</label>
                </div>
        </div>
        <div className="Songs hide">
            <p>Songs</p>
            <div className="song_container">
            <label>asas</label><label>dasdas</label><label>asdasas</label>
                    <label>asas</label><label>dasdas</label><label>asdasas</label>
                    <label>asas</label><label>dasdas</label><label>asdasas</label>
            </div>
        </div>
        </div>
    </div>
  )
}

export default Middlebar