import React,{useState,useEffect} from 'react'
import './bottombar.css'
import ListIcon from '@mui/icons-material/List';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import VolumeUpIcon from '@mui/icons-material/VolumeUp';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import PauseIcon from '@mui/icons-material/Pause';
import SkipNextIcon from '@mui/icons-material/SkipNext';
import SkipPreviousIcon from '@mui/icons-material/SkipPrevious';
import RefreshIcon from '@mui/icons-material/Refresh';
import ShuffleIcon from '@mui/icons-material/Shuffle';
const Botombar = () => {
  const [audio, setAudio] = useState(null);
  const [progress, setProgress] = useState(0);
  const [progresss, setProgresss] = useState(0.00);
  const [duration, setDuration] = useState(0.00);
  
  useEffect(() => {
    const audioElement = document.getElementById('myAudio');
    setAudio(audioElement);

    const handleTimeUpdate = () => {
      setDuration(parseFloat(audioElement.duration/60).toFixed(2));
      const newProgress = (audioElement.currentTime / audioElement.duration) * 100;
      setProgress(newProgress);
      setProgresss((audioElement.currentTime/60).toFixed(2));
    };

    audioElement.addEventListener('timeupdate', handleTimeUpdate);
    
    
    // return () => {
    //   audioElement.removeEventListener('timeupdate', handleTimeUpdate);
    // };
  }, []);
  const handleWidth=(event)=>{
    const divWidth = event.target.clientWidth;
    const clickX = event.clientX - event.target.offsetLeft;
    const clickper = (clickX / divWidth) * 100;
    setProgress(clickper)
    audio.currentTime=(clickper/100)*audio.duration;
    // audio.play();
  }
  const handleRestart = () => {
    audio.currentTime = 0;
    audio.play();
    document.querySelector('.play-btn').style.display="none";
      document.querySelector('.pause-btn').style.display="flex";
  };
  const handlePlayPause = () => {
    if (audio.paused) {
      audio.play();
      document.querySelector('.play-btn').style.display="none";
      document.querySelector('.pause-btn').style.display="flex";
    } else {
      audio.pause();
      document.querySelector('.play-btn').style.display="flex";
      document.querySelector('.pause-btn').style.display="none";
    }
  };
  return (
    <div className='bottombar'>
      <div className="song_detail">
        <img src="./images/sholay.jpg" alt="" /> <div><label className='song_name'>Jheeni Jheeni</label><label className='artist_name'>Arijit Singh</label></div>
      </div>
      <div className="player">
        <div className="player_icon"><ShuffleIcon className='icon_pl'/><SkipPreviousIcon className='icon_pl'/><div className="play-pause"><PlayArrowIcon className='play-btn' onClick={handlePlayPause} style={{backgroundColor:"#cfc7c7",borderRadius:"50%",color:"black",cursor:"pointer"}}/><PauseIcon onClick={handlePlayPause} className='pause-btn' style={{display:"none",backgroundColor:"#cfc7c7",borderRadius:"50%",color:"black",cursor:"pointer"}}/></div><SkipNextIcon className='icon_pl'/><RefreshIcon onClick={handleRestart} className='icon_pl'/></div>
        <div className="player_stats">
        <audio id="myAudio" src="./audio/yedil.mp4"></audio>
          <label>{progresss}</label><div className="pl_stat" onClick={handleWidth}><div onClick={handleWidth} className='song_stat' style={{width: `${progress}%`,cursor:"pointer"}}></div></div><label>{duration}</label>
        </div>
      </div>
      <div className="extra_detail"><VolumeUpIcon style={{fontSize:30,color:"white",marginRight:10}}/><ListIcon style={{marginRight:10,fontSize:30,color:"white"}}/></div>
    </div>
  )
}

export default Botombar