import React from 'react'
import './sidebar.css'
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import HomeIcon from '@mui/icons-material/Home';
import FavoriteIcon from '@mui/icons-material/Favorite';
import FormatListBulletedIcon from '@mui/icons-material/FormatListBulleted';
import SearchIcon from '@mui/icons-material/Search';
import LogoutIcon from '@mui/icons-material/Logout';
import QueueMusicIcon from '@mui/icons-material/QueueMusic';
const Sidebar = () => {
  return (
    <div className='sidebar'>
        <AccountCircleIcon className='sideIcon'/>
        <HomeIcon className='sideIcon'/>
        <SearchIcon className='sideIcon'/>
        <QueueMusicIcon className='sideIcon'/>
        <FavoriteIcon className='sideIcon' style={{color:"red"}}/>
        <LogoutIcon className='sideIcon'/>
    </div>
  )
}

export default Sidebar